"use client";

import { useState, FormEvent } from 'react';
import Layout from '../layout';
import MaintenanceLayout from './MaintenanceLayout';

// Removed metadata export to comply with Next.js requirements for client components.
// export const metadata = { ... };

export default function Maintenance() {
    const [dbName, setDbName] = useState('');

    const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        try {
            const response = await fetch('/api/maintainDB', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ dbName }),
            });

            const data = await response.json();
            alert(data.message || data.error);
        } catch (error) {
            console.error('Error:', error);
            alert('Failed to create database. Please try again.');
        }
    };

    return (
        <MaintenanceLayout>
            <Layout>
                <h1>Database Maintenance</h1>
                <form onSubmit={handleSubmit} style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
                    <input
                        type="text"
                        value={dbName}
                        onChange={(e) => setDbName(e.target.value)}
                        placeholder="Enter database name"
                        required
                    />
                    <button type="submit">Create Database</button>
                </form>
            </Layout>
        </MaintenanceLayout>
    );
}
